import { useSelector, useDispatch } from 'react-redux'
import { Line } from 'react-chartjs-2';

import getChartData from '../../getChartData';
import getImg from '../../getImg';

import './index.scss';
import { useEffect, useRef, useState } from 'react';
import waveColor from '../../waveColor';

const PIXEL_PER_METR = 740/7;

function Info(){
    const wavelength = useSelector(state => state.L);
    const S = useSelector(state => state.S);
    const D = useSelector(state => state.D);

    let data = getChartData(wavelength, D, S);

    return(
        <div className="info">
            <h2>Результаты</h2>
            <h3>Распределение интенсивности <span>при дифракции на щели</span></h3>
            <Line  
                className="info__chart"
                width="1200px"
                height="400px"
                
                data={{
                    labels: data.labels,
                    datasets: [
                        {
                            label: 'I(x)',
                            data: data.data,
                            fill: false,
                            backgroundColor: "rgba(189, 195, 199,1.0)",
                            borderColor: 'rgba(0,0,0)',
                        },
                    ]
                }}

                options={{
                    responsive: false,
                    scales: {
                        y: {
                            title: {
                                display: true,
                                text: 'I',
                                font: {
                                    size: 16,
                                    weight: 'bold'
                                }
                            }
                        },
                        x: {
                            ticks: {

                            },
                            title: {
                                display: true,
                                text: 'θ(рад)',
                                font: {
                                    size: 16,
                                    weight: 'bold'
                                }
                            }
                        }
                    }
                }}
            />

            {/* <h3>дифракционная картина</h3> */}

            <div
                className="info__img"
                style={{
                    backgroundImage: getImg(wavelength, D, S)
                }}
            ></div>
        </div>
    );
}

export default Info;