import { useCallback, useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux'

import action from '../../actions/creator'; 

import ModelParamsItem from './ModelParamsItem';

import { Range } from 'react-range';

export default function ModelParams(callbacks){
    const dispatch = useDispatch();

    const L = useSelector(state => state.L);
    const S = useSelector(state => state.S);
    const D = useSelector(state => state.D);

    const [range, setRange] = useState({
        min: 0,
        max: 1,
        units: '??',
        value: 0,
        inputName: ''
    });

    
    let setInput = (min, max, value, units, inputName) => {
        let range_ = {...range};
        range_.min = min;
        range_.max = max;
        range_.value = value;
        range_.units = units;
        range_.inputName = inputName;

        setRange(range_);
    };



    useEffect(() => {
        if(range.inputName == 'λ'){
            dispatch(action.setL(range.value));
        }else if(range.inputName == 'S'){
            dispatch(action.setS(range.value));
        }else if(range.inputName == 'D'){
            dispatch(action.setD(range.value));
        }
    }, [range]);

    return(
        <div className="model-params">
            <div className="model-params-item-wrapper">
                <ModelParamsItem name="λ" units="нм" min={380} max={750} value={L} setInput={setInput} range={range} />
                <ModelParamsItem name="S" units="м" min={0.44} max={7.5} value={S} setInput={setInput} range={range} />
                <ModelParamsItem name="D" units="нм" min={750} max={5000} value={D} setInput={setInput} range={range} />
                <ModelParamsItem name="h" units="м" min={500} max={5000} value={3.8} setInput={setInput} range={range} disabled />
            </div>
            <div className={"model-params-input " + (range.inputName == '' ? 'disabled' : '')}>
                <div className="model-params-input-inner-wrapper">
                    <div className="model-params-input-value">{range.min}</div>
                    <Range
                        step={0.1}
                        min={range.min}
                        max={range.max}
                        values={[range.value]}
                        onChange={(values) => {
                            let range_ = {...range};
                            range_.value = values[0];
                            setRange(range_);
                        }}

                        renderTrack={({ props, children }) => (
                            <div
                                {...props}
                                style={{
                                    ...props.style,
                                    height: '6px',
                                    borderRadius: '2px',
                                    width: '330px',
                                    backgroundColor: '#ccc',
                                    marginTop: '11px',
                                    marginLeft: '15px',
                                    marginRight: '15px'
                                }}
                            >
                                {children}
                            </div>
                        )}

                        renderThumb={({ props }) => (
                            <div
                                {...props}
                                style={{
                                    ...props.style,
                                    height: '15px',
                                    width: '15px',
                                    borderRadius: '100px',
                                    backgroundColor: '#fff',
                                    outline: 'none',
                                    boxShadow: 'rgba(0, 0, 0, 0.3) 0 1px 3px'
                                }}
                            />
                        )}
                    />

                    <div className="model-params-input-value">{range.max}</div>
                </div>
                <div className="model-params-input-units">{range.units}</div>
            </div>
        </div>
    );
}