import actions from './index';

export default {
    setL: value => {
        return {
            type: actions.SET_L,
            value: value
        }
    },
    setD: value => {
        return {
            type: actions.SET_D,
            value: value
        }
    },
    setS: value => {
        return {
            type: actions.SET_S,
            value: value
        }
    }
}