export default function getChartData(waveL, D, S){
    let wavelength = waveL*1e-9;
    let b = D * 1e-9;

    let labels = [];
    let intensities = [];

    function max(n){
        return Math.asin((2*n+1)*wavelength/2/b)
    }

    function intense(angle){
        return Math.sin(Math.PI*b*Math.sin(angle)/wavelength)**2/(Math.PI*b*Math.sin(angle)/wavelength)**2;
    }
    
    let w = 1.9;
    let maxAngle = Math.atan(w/S);

    for(var i = -maxAngle; i < maxAngle; i += 0.01){
        labels.push(i.toFixed(2));
        intensities.push(intense(i));
    }

    return {
        labels: labels,
        data: intensities,
    }
}