export default function ModelParamsItem({name, units, min, max, value, setInput, range, disabled, style}){
    let onClick = () => {
        let selected = name == range.inputName;
        if(!selected){
            setInput(min, max, value, units, name);
        }else{
            setInput(min, max, value, units, '');
        }
    };

    let selected = name == range.inputName;

    return(
        <div className={"model-params-item " + (selected ? "selected" : "") + (disabled ? "disabled" : "")} onClick={onClick} style={{...style}}>
            <div className="model-params-item__value">{value.toFixed(1)}</div>
            <div className="model-params-item__units">{units}</div>
            <div className="model-params-item__name">{name}</div>
        </div>
    );
}