export default function lines(lambda, b, S){
    var RAD = 1000;  

    function maximum(k) {
        if (k == 0) return 0;

        var l = k*Math.PI;
        var r = (k+0.5)*Math.PI;
        var m = (l+r)/2;

        while (r-l > 1e-10) {
            if (Math.tan(m) > m) r = m; else l = m;
            m = (l+r)/2;
        }

        if (m*lambda > Math.PI*b) return -1;

        return Math.asin(m*lambda/(Math.PI*b));
    }
        

    function raysMaxima() {
        let lines = [];

        var maxK = Math.floor(b/lambda+0.5);

        for (var k=1; k<=maxK; k++) { 
            var w = maximum(k);
            if (w < 0) break;
            var x = RAD*Math.cos(w), y = RAD*Math.sin(w); 

            lines.push({
                x, y
            });
        }

        return lines;
    }

    return raysMaxima();
}