import { useCallback, useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { useResizeDetector } from 'react-resize-detector';

import waveColor from '../../waveColor';
import lines from '../../lines';
import ModelParams from '../ModelParams';

import getImg from '../../getImg';

import './index.scss';

const PIXEL_PER_METR = 740/7;

function Model(){
    const wavelength = useSelector(state => state.L);
    const S = useSelector(state => state.S);
    const D = useSelector(state => state.D);

    const [wavesSvgWidth, setWavesSvgWidth] = useState(200);
    
    const { width, height, ref } = useResizeDetector();
    
    useEffect(() => {
        if(!!width)
        setWavesSvgWidth(width)
    }, [width])

    let waves = [];

    let linePos = lines(wavelength*1e-9, D*1e-9);
    
    let lightWidth = 100;
    let lightbetweenRange = 30;



    function max(n){
        return Math.asin((2*n+1)*wavelength/2/D)
    }


    let angle = [];

    for(let i = 0; i < 3; i++){
        angle.push(max(i));
    }

    angle.forEach(item => {
        waves.push(<line x1="0" y1="200" x2={S*1000} y2={1000*S*Math.sin(item)+200} stroke={"#bdc3c7"} strokeWidth={`2px`} />);
        waves.push(<line x1="0" y1="200" x2={S*1000} y2={-1000*S*Math.sin(item)+200} stroke={"#bdc3c7"} strokeWidth={`2px`} />);
    });

    waves.push(<line x1="0" y1="200" x2={1000} y2={200} stroke={"#bdc3c7"} strokeWidth={`2px`} />);

    return(
        <div className="model">
            <h2><span className="grad">Дифракция</span> электронов<br/> на одиночной щели</h2>
            <div className="model-inner-wrapper">
                <div className="model-scheme">
                    <div className="model-scheme__light"
                        onClick={() => {
                            document.querySelectorAll('.model-params-item ')[0].click();
                        }}
                    >
                        <svg>                  
                            {(new Array(Math.ceil(400/lightbetweenRange)).fill(0)).map((item, index) => {
                                return <path
                                            key={index}
                                            d={`M 0 ${lightbetweenRange*index + 5} h ${lightWidth-2} l -5 -5 m 5 5 l -5 5`}
                                            fill="none"
                                            opacity="1" 
                                            stroke={"#3498db"}
                                            strokeWidth="3px"
                                            markerStart="url(#electronstart)"
                                        >
                                        </path>
                            })}
                        </svg>
                    </div>
                    <div
                        className="model-scheme__slit"
                        onClick={() => {
                            document.querySelectorAll('.model-params-item ')[1].click();
                        }}
                    >
                        <div className="model-scheme__slit__wall">
                            <div
                                className="model-scheme__slit__wall__slit"
                                style={{
                                    height: 1e-3*D
                                }}
                            ></div>
                        </div>
                        <div className="model-scheme__slit__name">
                            щель
                        </div>
                    </div>
                    <div
                        style={{
                            width: PIXEL_PER_METR*S
                        }}
                        ref={ref}
                        className="model-scheme__waves"
                    >
                        <svg>
                            <defs>
                                <marker id="startarrow" markerWidth="10" markerHeight="7" 
                                refX="5" refY="1.5" orient="auto">
                                    <polygon points="5 0, 5 3, 0 1.5" fill="#000000" />
                                </marker>
                                <marker id="endarrow" markerWidth="10" markerHeight="7" 
                                refX="0" refY="1.5" orient="auto" markerUnits="strokeWidth">
                                    <polygon points="0 0, 5 1.5, 0 3" fill="#000000" />
                                </marker>
                            </defs>

                            <text x="24" y="400" style={{fontSize: 22, fontWeight: 'bold'}} textAnchor="middle">S</text>

                            <text x={S*PIXEL_PER_METR/2} y="10%" style={{fontSize: 22, fontWeight: 'bold'}} textAnchor="middle">h</text>

                            <line x1={S*PIXEL_PER_METR/2+15} x2={S*PIXEL_PER_METR/2+15} y1="387" y2="15" strokeWidth="3px" stroke="#000000" marker-end="url(#endarrow)" markerStart="url(#startarrow)" />
                            <line x1="15" x2={S*PIXEL_PER_METR - 15} y1="380" y2="380" strokeWidth="3px" stroke="#000000" marker-end="url(#endarrow)" markerStart="url(#startarrow)" />   
                            {waves}
                        </svg>
                    </div>
                    <div
                        className="model-scheme__screen"
                        onClick={() => {
                            document.querySelectorAll('.model-params-item ')[2].click();
                        }}
                    >
                        <div
                            className="model-scheme__screen__wall"
                            style={{
                                backgroundImage: getImg(wavelength, D, S).replace('90deg', '180deg')
                            }}
                        ></div>
                        <div className="model-scheme__screen__name">
                            фотопластина
                        </div>
                    </div>
                </div>
                <ModelParams />
            </div>
        </div>
    );
}

export default Model;