import { createStore } from 'redux';

let initialState = {
    L: 500,
    D: 2075,
    S: 5
};

function reducer(state, action) {
    switch(action.type) {
        case "SET_L": return {...state, L: parseInt(action.value)};
        case "SET_D": return {...state, D: parseFloat(action.value)};
        case "SET_S": return {...state, S: parseFloat(action.value)};
        
        default: return state;
    }
}

const store = createStore(reducer, initialState);

export default store;