export default function drawMonoInterfPicture(waveL, D, S) {
    // wavelength *= 1e9;
    // b *= 1e5;
    // L *= 170;

    // function intensityFunction(x) {
    //     let e = 1e-6;
    //     let u = (Math.PI * b * x) / (wavelength * e * L);
    //     let q = (Math.PI * d * x) / (wavelength * e * L);
    //     return Math.pow(b, 2) * Math.pow(Math.sin(u) / u, 2) * Math.pow(Math.sin(q) / Math.sin(q), 2)
    // }

    // let labels = [];


    // for (let x = -3/2; x <= 3/2; x+=3/497) {
    //     labels.push(x);
    // }

    // let //ctx = canvas.getContext("2d"),
    //     height = 400,
    //     intensities = [];

    // labels.forEach(function(x) {
    //     intensities.push(intensityFunction(x))
    // });

    // let yStep = width / labels.length;

    // let Max = intensities[labels.length / 2 ];


    let gradientArr = [];
    let patternStyle = null;

    let wavelength = waveL*1e-9;
    let b = D * 1e-9;

    let labels = [];
    let intensities = [];

    function max(n){
        return Math.asin((2*n+1)*wavelength/2/b)
    }

    function intense(angle){
        return Math.sin(Math.PI*b*Math.sin(angle)/wavelength)**2/(Math.PI*b*Math.sin(angle)/wavelength)**2;
    }
    
    let w = 1.9;
    let maxAngle = Math.atan(w/S);

    for(var i = -maxAngle; i < maxAngle; i += 0.01){
        labels.push(i.toFixed(2));
        intensities.push(intense(i));
    }
    

    labels.forEach(function(x, index) {
        const alpha = intensities[index] / 1;
        //gradientArr.push(`${rgbColor.replace(')', `, ${alpha})`).replace('rgb', 'rgba')} ${((x-labels[0])/(labels[labels.length - 1] - labels[0]))*100}%`);
        gradientArr.push(`rgba(52, 152, 219, ${alpha}) ${((x-labels[0])/(labels[labels.length - 1] - labels[0]))*100}%`);
        patternStyle = 'linear-gradient(90deg, '+gradientArr.join(',') + ')';
    });

    return patternStyle;
}